const feedbackResponse = document.querySelector('#feedback-response');
const feedbackForm = document.forms.feedback;

feedbackForm.addEventListener('submit', (evt) => {
  evt.preventDefault();

  feedbackResponse.innerHTML = 'отправляем...';

  const xhr = new XMLHttpRequest();
  const data = new FormData(feedbackForm);

  // xhr.open(null, null);
  // xhr.open('POST', 'https://precedent.herokuapp.com/');
  xhr.open(feedbackForm.method, feedbackForm.action);

  xhr.onreadystatechange = function () {
    if (xhr.readyState === XMLHttpRequest.DONE && xhr.status === 200) {
      const answer = JSON.parse(xhr.responseText);

      feedbackResponse.innerHTML = `
        Уважаемый <b>${answer.name}</b>,<br>
        Ваш запрос по теме принят <b>${answer.message}</b> и отправлен на почту<br>
        специалисту <b>${answer.employee}</b>,<br>
        который свяжется с Вами по <b>${answer.contact}</b><br>
      `;

    }
  };
  xhr.send(data);

});



